import React, { useContext, useState } from "react";
import styled from "styled-components";
import { withRouter } from "react-router-dom";
import { TeamsContext } from "../contexts/TeamsContext";
import { PlayersContext } from "../contexts/PlayersContext";
import { Title } from "../components/Title";
import { Button } from "../components/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import ConfirmButtons from "../components/ConfirmButtons";
import TeamsTable from "../components/TeamsTable";
import { ScoresContext } from "../contexts/ScoresContext";

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 0 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 300px;
  .dashboard {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .ul-wrapper {
      min-height: 4vw;
      width: 100%;
      overflow-x: auto;
      overflow-y: hidden;
    }
  }
`;
const AmountOfTeams = styled(Title)`
  padding: 10px 0;
  position: relative;
  transform: translate(0);
  left: 0;
`;

const StyledTitle = styled(Title)`
  position: static;
  text-align: center;
  margin: 20px auto 10px;
  left: auto;
  transform: translate(0);
  ::after {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    content: "";
    width: 350%;
    height: 2px;
    background-color: white;
    display: block;
    margin: 10px auto;
  }
`;

const ManageButtons = styled.div`
  margin: 10px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 340px;

  button {
    font-size: 12px;
    height: auto;
    min-height: 30px;
    padding: 5px 15px;
    margin: 0 5px;
    @media only screen and (min-width: 1024px) {
      height: auto;
    }
  }
`;

const Radio = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;

  div {
    margin: 0 10px;
    color: white;
    input {
      margin: 0 5px;
    }
  }
`;

const ChooseTeams = (props) => {
  const {
    teams,
    setTeamSelected,
    teamsInBasket,
    setTeamsInBasket,
    addAllTeamsToBasket,
  } = useContext(TeamsContext);

  const { unsubmitNewPlayersForm, clearPlayers } = useContext(PlayersContext);
  const { setTournamentStart } = useContext(ScoresContext);
  const [radioValue1, setRadioValue1] = useState(true);
  const [radioValue2, setRadioValue2] = useState(false);

  const handleConfirm = () => {
    if (teamsInBasket.length > 0) {
      if (teamsInBasket.length % 2 !== 0) {
        alert("Wybierz parzyst liczb druyn!");
        return;
      } else if (teamsInBasket.length % 2 === 0) {
        props.history.push("/tournament");
        setTournamentStart(true);
      }
    } else alert("Wybierz druyny!");
    return;
  };

  const goBackFunction = () => {
    unsubmitNewPlayersForm();
    clearPlayers();
  };

  const handleTeamClick = (e) => {
    e.target.parentNode.classList.toggle("selected");
    setTeamSelected(e.target.alt);
  };

  const addAllToBasket = () => {
    const teams = window.document.querySelectorAll(".team-in-basket");
    teams.forEach((team) => team.classList.add("selected"));
    addAllTeamsToBasket();
  };
  const deleteAllFromBasket = () => {
    const teams = window.document.querySelectorAll(".team-in-basket");
    teams.forEach((team) => team.classList.remove("selected"));
    setTeamsInBasket([]);
  };

  const handleRadioChange = (e) => {
    setRadioValue1(!e.target.checked);
    setRadioValue2(!e.target.checked);
  };
  return (
    <Wrapper>
      <div className={"dashboard"}>
        <StyledTitle>Wybierz druyny :</StyledTitle>
        <div className="ul-wrapper">
          <TeamsTable newcup>
            {teams.map((team) => (
              <li key={team.id} onClick={handleTeamClick}>
                <div className={"team-in-basket"}>
                  <img
                    src={team.img}
                    alt={team.team}
                    className={team.class}
                  ></img>
                  <FontAwesomeIcon icon={faCheckCircle} />
                </div>
              </li>
            ))}
          </TeamsTable>
        </div>
        <Radio>
          <div>
            <input
              type="radio"
              id="clubs"
              name="teams"
              value="clubs"
              checked={radioValue1}
              onChange={handleRadioChange}
            />
            <label htmlFor="clubs">Kluby</label>
          </div>

          <div>
            <input
              checked={radioValue2}
              type="radio"
              id="country"
              name="teams"
              value="country"
              onChange={handleRadioChange}
            />
            <label htmlFor="country">Reprezentacje</label>
          </div>
        </Radio>

        <AmountOfTeams small>
          Ilo wybranych druyn: {teamsInBasket.length}
        </AmountOfTeams>
        <ManageButtons>
          <Button small onClick={addAllToBasket}>
            Dodaj wszystkie do koszyka
          </Button>
          <Button small onClick={deleteAllFromBasket}>
            Usu zanaczenie
          </Button>
        </ManageButtons>
      </div>

      <ConfirmButtons
        textButton={"Utwrz turniej"}
        goBackFunction={goBackFunction}
        handleConfirm={handleConfirm}
      />
    </Wrapper>
  );
};

export default withRouter(ChooseTeams);
